import { Spin, Breadcrumb, Button, Table, Pagination } from "antd";
import React, { useState, useEffect } from "react";
import Title from "antd/lib/typography/Title";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { actShowLoading, actHideLoading } from "../../../actions/actionLoading";
import { fetchPhieuThu } from "../../../actions/actionPhieuThu";
import { FetchURL } from "../../../util/help";

function PhieuThu() {
  const [state, setState] = useState({ total_page: 1 });

  const isLoading = useSelector((state) => state.isLoading);
  const phieuthu = useSelector((state) => state.phieuthu);

  const dispatch = useDispatch();
  const showLoading = () => dispatch(actShowLoading());
  const hideLoading = () => dispatch(actHideLoading());
  const Fetch = (data) => dispatch(fetchPhieuThu(data));

  const columns = [
    {
      title: "Receipt No",
      dataIndex: "RECEIPT_NO",
      key: "RECEIPT_NO",
    },
    {
      title: "NgÆ°á»i Nháº­p",
      dataIndex: "INPUT_USER",
      key: "INPUT_USER",
    },
    {
      title: "TÃªn CÃ´ng Ty",
      dataIndex: "CUST_NAME",
      key: "CUST_NAME",
    },
  ];

  useEffect(() => {
    onChangePage();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onChangePage = (page) => {
    var newPage = 1;
    if (page) {
      newPage = page;
    }

    showLoading();
    fetch(FetchURL + `payment/receipts/page=${newPage}`)
      .then((res) => res.json())
      .then((data) => {
        if (data.success === true) {
          Fetch(data.data);
          setState({ total_page: data.total_page });
          hideLoading();
        }
      })
      .catch((err) => console.log(err));
  };

  return (
    <Spin size="large" spinning={isLoading}>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>Quáº£n LÃ½ Thu Chi</Breadcrumb.Item>
        <Breadcrumb.Item>Phiáº¿u Thu</Breadcrumb.Item>
      </Breadcrumb>
      <div
        className="site-layout-background"
        style={{ padding: 24, minHeight: 360 }}
      >
        <div style={{ textAlign: "center" }}>
          <Title style={{ color: "#1890ff" }}>Phiáº¿u Thu</Title>
        </div>
        <Button type="primary" style={{ marginBottom: 20 }}>
          <Link to="/Dasboard/phieuthu/create">Táº¡o má»i</Link>
        </Button>
        <Table
          dataSource={phieuthu}
          columns={columns}
          rowKey={columns[0].key.toString()}
          pagination={false}
        />
        <Pagination
          total={state.total_page}
          defaultCurrent={1}
          onChange={onChangePage}
          style={{ marginTop: 20 }}
        />
      </div>
    </Spin>
  );
}

export default PhieuThu;
