import React, { Fragment, useEffect, useState } from "react";
import {
  Table,
  Input,
  InputNumber,
  Popconfirm,
  Form,
  Select,
  Button,
  Spin,
  Row,
  Col,
} from "antd";
import "./tableEdit.css";
import {
  payDescription,
  openNotificationWithIcon,
  convertNumber,
} from "../../../../util/help";
import axios from "../../../../util/api";
import { useSelector, useDispatch } from "react-redux";
import {
  actHideLoading,
  actShowLoading,
} from "../../../../actions/actionLoading";

const { Option } = Select;

const CreatItemPhieuChiTamUng = ({ job_d, id, getDataDetail }) => {
  const [form] = Form.useForm();
  const [state, setState] = useState({ data: [] });
  const [serNo, setSerNo] = useState("");

  const isLoading = useSelector((state) => state.isLoading);

  const dispatch = useDispatch();

  const showLoading = () => dispatch(actShowLoading());
  const hideLoading = () => dispatch(actHideLoading());

  useEffect(() => {
    var newData = job_d;
    for (var i = 0; i < newData.length; i++) {
      newData[i].key = i;
    }
    setState({ ...state, data: newData });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [job_d]);

  const edit = (record) => {
    form.setFieldsValue({
      LENDER_AMT: record.LENDER_AMT,
      NOTE: record.NOTE,
    });
    setSerNo(record.SER_NO);
  };

  const Delete = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...state.data];
      const index = newData.findIndex((item) => key.key === item.key);

      if (index > -1) {
        const params = {
          LENDER_NO: id,
          SER_NO: newData[index].SER_NO,
        };
        showLoading();
        axios
          .post("payment/lender/remove-d", params)
          .then((res) => {
            if (res.status === 200) {
              if (res.data.success === true) {
                newData.splice(index, 1);
                setState({ ...state, data: newData });
                hideLoading();
                openNotificationWithIcon("success", res.data.message);
              }
            }
          })
          .catch((err) => console.log(err));
      }
    } catch (errInfo) {
      console.log("Validate Failed:", errInfo);
    }
  };

  const columns = [
    {
      title: "Tiá»n",
      dataIndex: "LENDER_AMT",
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Ghi ChÃº",
      dataIndex: "NOTE",
    },
    {
      title: "Action",
      dataIndex: "operation",
      render: (_, record) => {
        return (
          <>
            <Button type="link" onClick={() => edit(record)}>
              Edit
            </Button>

            <Popconfirm
              title="Báº¡n cÃ³ cháº¯n cháº¯n xÃ³a?"
              onConfirm={() => Delete(record)}
            >
              <Button type="link">Delete</Button>
            </Popconfirm>
          </>
        );
      },
    },
  ];

  const setField = () => {
    form.setFieldsValue({
      LENDER_AMT: "",
      NOTE: "",
    });
    setSerNo("");
  };

  function onFinish(values) {
    values.SER_NO = serNo;
    values.LENDER_NO = id;
    values.BRANCH_ID = localStorage.getItem("BRANCH_ID");
    if (serNo) {
      values.MODIFY_USER = localStorage.getItem("USER_NO");
      showLoading();
      axios
        .post("payment/lender/edit-d", values)
        .then((res) => {
          if (res.status === 200) {
            if (res.data.success === true) {
              getDataDetail(id);
              setSerNo("");
              setField();
              hideLoading();
              openNotificationWithIcon("success", "Chá»nh Sá»­a ThÃ nh CÃ´ng");
            }
          }
        })
        .catch((err) => console.log(err));
    } else {
      values.INPUT_USER = localStorage.getItem("USER_NO");
      showLoading();
      axios
        .post("payment/lender/add-d", values)
        .then((res) => {
          if (res.status === 200) {
            if (res.data.success === true) {
              getDataDetail(id);
              setField();
              hideLoading();
              openNotificationWithIcon("success", "Táº¡o ThÃ nh CÃ´ng");
            }
          }
        })
        .catch((err) => console.log(err));
    }
  }

  return (
    <Fragment>
      <Form onFinish={onFinish} form={form}>
        <Row>
          <Col>
            <Form.Item label="Tiá»n" name="LENDER_AMT">
              <InputNumber
                formatter={(value) =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
                style={{ width: 200 }}
              />
            </Form.Item>
          </Col>
          <Col>
            <Form.Item label="Ghi ChÃº" style={{ marginLeft: 10 }} name="NOTE">
              <Input />
            </Form.Item>
          </Col>
          <Col>
            <Form.Item>
              <Button
                accessKey="s"
                type="primary"
                htmlType="submit"
                style={{ marginLeft: 20 }}
              >
                {serNo ? "Sá»­a" : "ThÃªm"}
              </Button>
              {serNo ? (
                <Button type="link" onClick={setField}>
                  Cancel
                </Button>
              ) : null}
            </Form.Item>
          </Col>
        </Row>
      </Form>
      <Spin size="large" spinning={isLoading}>
        <Table dataSource={state.data} columns={columns} pagination={false} />
      </Spin>
    </Fragment>
  );
};

export default CreatItemPhieuChiTamUng;
