import React, { Fragment, useEffect, useState } from "react";
import {
  Table,
  Input,
  InputNumber,
  Popconfirm,
  Form,
  Select,
  Button,
  Spin,
  Row,
  Col,
} from "antd";
import "./tableEdit.css";
import {
  orderType,
  openNotificationWithIcon,
  convertNumber,
  handleEnter,
  onKeyDown,
} from "../../../util/help";
import axios from "../../../util/api";
import { useSelector, useDispatch } from "react-redux";
import { actHideLoading, actShowLoading } from "../../../actions/actionLoading";

const { Option } = Select;

const CreateItemJobBookTau = ({ job_d, id, getDataDetail }) => {
  const [form] = Form.useForm();
  const [state, setState] = useState({ data: [] });
  const [editingKey, setEditingKey] = useState("");
  const [changeSelect, setChangSelect] = useState("select");
  const [serNo, setSerNo] = useState("");

  const isLoading = useSelector((state) => state.isLoading);

  const dispatch = useDispatch();

  const showLoading = () => dispatch(actShowLoading());
  const hideLoading = () => dispatch(actHideLoading());

  useEffect(() => {
    var newData = job_d;
    for (var i = 0; i < newData.length; i++) {
      newData[i].key = i;
    }
    setState({ ...state, data: newData });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [job_d]);


  const edit = (record) => {
    form.setFieldsValue({
      ORDER_TYPE: record.ORDER_TYPE,
      ORDER_TYPE_NAME: record.ORDER_TYPE_NAME,
      DESCRIPTION: record.DESCRIPTION,
      UNIT: record.UNIT,
      QTY: record.QTY,
      PRICE: record.PRICE,
      TAX_NOTE: record.TAX_NOTE,
      SAU_THUE: record.SAU_THUE,
      TAX_AMT: record.TAX_AMT,
      TONG_TIEN: record.TONG_TIEN,
      NOTE: record.NOTE,
    });
    setSerNo(record.SER_NO)
  };

  const getDataField = (record) => {
    var giaTruocThue = form.getFieldsValue().PRICE
      ? form.getFieldsValue().PRICE
      : 0;
    var sl = form.getFieldsValue().QTY;
    var thue = parseInt(form.getFieldsValue().TAX_NOTE);
    var tienThue = (giaTruocThue * thue) / 100;
    var giaSauThue = giaTruocThue + tienThue;
    var tongTien = sl * giaSauThue;
    form.setFieldsValue({
      TAX_AMT: tienThue, //Tiá»n Thuáº¿
      SAU_THUE: giaSauThue,
      TONG_TIEN: tongTien,
    });
  };

  const Delete = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...state.data];
      const index = newData.findIndex((item) => key.key === item.key);

      if (index > -1) {
        const params = {
          JOB_NO: id,
          ORDER_TYPE: newData[index].ORDER_TYPE,
          SER_NO: newData[index].SER_NO,
        };
        showLoading();
        axios
          .post("file/job-order/remove-d", params)
          .then((res) => {
            if (res.status === 200) {
              if (res.data.success === true) {
                newData.splice(index, 1);
                setState({ ...state, data: newData });
                setEditingKey("");
                hideLoading();
                openNotificationWithIcon("success", res.data.msg);
              }
            }
          })
          .catch((err) => console.log(err));
      }
    } catch (errInfo) {
      console.log("Validate Failed:", errInfo);
    }
  };

  const columns = [
    {
      title: "Loáº¡i",
      dataIndex: "ORDER_TYPE_NAME",
    },
    {
      title: "MÃ´ táº£",
      dataIndex: "DESCRIPTION",
    },
    {
      title: "ÄVT",
      dataIndex: "UNIT",
    },
    {
      title: "SL",
      dataIndex: "QTY",
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "GiÃ¡ TrÆ°á»c Thuáº¿",
      dataIndex: "PRICE",
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Thuáº¿",
      dataIndex: "TAX_NOTE",
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "GiÃ¡ Sau Thuáº¿",
      dataIndex: "SAU_THUE",
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Tiá»n Thuáº¿",
      dataIndex: "TAX_AMT",
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Tá»ng Tiá»n",
      dataIndex: "TONG_TIEN",
      render: (text) => convertNumber(parseFloat(text)),
    },
    {
      title: "Ghi ChÃº",
      dataIndex: "NOTE",
    },
    {
      title: "Action",
      dataIndex: "operation",
      render: (_, record) => {
        return (
          <>
            <Button type="link" onClick={() => edit(record)}>
              Edit
            </Button>

            <Popconfirm
              title="Báº¡n cÃ³ cháº¯n cháº¯n xÃ³a?"
              onConfirm={() => Delete(record)}
            >
              <Button type="link">Delete</Button>
            </Popconfirm>
          </>
        );
      },
    },
  ];

  function onFinish(values) {
    const typeTest = orderType.find(
      (i) => i.name === values.ORDER_TYPE_NAME
    );
    values.ORDER_TYPE = typeTest.type;
    showLoading();
    if (serNo) {
      values.SER_NO = serNo
      values.TYPE = "JOB_ORDER_BOAT";
      values.JOB_NO = id;
      values.THANH_TOAN_MK = "N";
      values.MODIFY_USER = localStorage.getItem("USER_NO");
      values.REV_TYPE = "Y";
      axios
        .post("file/job-order/edit-d", values)
        .then((res) => {
          if (res.status === 200) {
            if (res.data.success === true) {
              getDataDetail(id);
              setField()
              hideLoading();
              openNotificationWithIcon("success", "Chá»nh Sá»­a ThÃ nh CÃ´ng");
            }
          }
        })
        .catch((err) => console.log(err));
    } else {
      values.TYPE = "JOB_ORDER_BOAT";
      values.JOB_NO = id;
      values.THANH_TOAN_MK = "N";
      values.BRANCH_ID = localStorage.getItem("BRANCH_ID");
      values.INPUT_USER = localStorage.getItem("USER_NO");
      axios
        .post("file/job-order/add-d", values)
        .then((res) => {
          if (res.status === 200) {
            if (res.data.success === true) {
              getDataDetail(id);
              setField()
              hideLoading();
              openNotificationWithIcon("success", "Táº¡o ThÃ nh CÃ´ng");
            }
          }
        })
        .catch((err) => console.log(err));
    }

  }

  const setField = () => {
    form.setFieldsValue({
      ORDER_TYPE: "",
      ORDER_TYPE_NAME: "",
      DESCRIPTION: "",
      UNIT: "",
      QTY: 0,
      PRICE: 0,
      TAX_NOTE: 0,
      SAU_THUE: 0,
      TAX_AMT: 0,
      TONG_TIEN: 0,
      NOTE: "",
    });
    setSerNo("")
  };

  function onSelect(values) {
    if (values === "Trucking Fee") {
      form.setFieldsValue({
        DESCRIPTION: "PHÃ KÃO",
      });
    }
  }

  function onChangeDes (values) {
    if ((values.target.value === "A") | (values.target.value === "a")) {
      form.setFieldsValue({
        DESCRIPTION: "AIR FREIGHT",
      });
    } else if ((values.target.value === "O") | (values.target.value === "o")) {
      form.setFieldsValue({
        DESCRIPTION: "OCEAN FREIGHT",
      });
    } else {
      form.setFieldsValue({
        DESCRIPTION: values.target.value ,
      });
    }
  }

  return (
    <Fragment>
      <Form onKeyDown={onKeyDown} form={form} onFinish={onFinish}>
        <Row gutter={8}>
          <Col xs={2} sm={16} md={12} lg={8} xl={10}>
            <Form.Item label="Loáº¡i" name='ORDER_TYPE_NAME' >
              <Select
                onKeyDown={handleEnter}
                style={{ width: 180 }}
                onSelect={onSelect}
              >
                <Option key="Our Company Pay">Our Company Pay</Option>
                <Option key="Pay In Advance">Pay In Advance</Option>
                <Option key="Trucking Fee">Trucking Fee</Option>
                <Option key="Deposit fee">CÆ°á»£c Cont</Option>
                <Option key="Deposit fix fee">CÆ°á»£c Sá»­a Chá»¯a Cont</Option>
                <Option key="Refund Customer">Refund KhÃ¡ch HÃ ng</Option>
                <Option key="Refund Carriers">Refund HÃ£ng TÃ u</Option>
                <Option key="Refund Agent">Refund Äáº¡i LÃ½</Option>
              </Select>
            </Form.Item>
            <Form.Item
              label="MÃ´ táº£"
              style={{ marginLeft: 10 }}
              name="DESCRIPTION"
            >
              <Input
                onKeyDown={handleEnter}
                style={{ width: 160 }}
                onChange={onChangeDes}
              />
            </Form.Item>
            <Form.Item label="ÄVT" style={{ marginLeft: 10 }} name="UNIT">
              <Input onKeyDown={handleEnter} style={{ width: 170 }} />
            </Form.Item>
            <Form.Item label="SL" name="QTY">
              <InputNumber
                onKeyDown={handleEnter}
                style={{ width: 190 }}
                formatter={(value) =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
                onChange={getDataField}
              />
            </Form.Item>
            <Form.Item
              label="GiÃ¡ trÆ°á»c thuáº¿"
              style={{ marginLeft: 10 }}
              name="PRICE"
            >
              <InputNumber
                onKeyDown={handleEnter}
                style={{ width: 110 }}
                formatter={(value) =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
                onChange={getDataField}
              />
            </Form.Item>
          </Col>
          <Col xs={2} sm={16} md={12} lg={8} xl={10}>
            <Form.Item
              label="Thuáº¿"
              style={{ marginLeft: 10 }}
              name="TAX_NOTE"
              initialValue={"0"}
            >
              <Select
                style={{ width: 220 }}
                onKeyDown={handleEnter}
                onSelect={getDataField}
              >
                <Option key={"0"}>0</Option>
                <Option key={"10"}>10</Option>
              </Select>
            </Form.Item>
            <Form.Item
              label="GiÃ¡ sau thuáº¿"
              style={{ marginLeft: 10 }}
              name="SAU_THUE"
            >
              <InputNumber
                onKeyDown={handleEnter}
                style={{ width: 170 }}
                formatter={(value) =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
              />
            </Form.Item>
            <Form.Item
              label="Tiá»n thuáº¿"
              style={{ marginLeft: 10 }}
              name="TAX_AMT"
            >
              <InputNumber
                onKeyDown={handleEnter}
                style={{ width: 190 }}
                formatter={(value) =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
                onChange={getDataField}
              />
            </Form.Item>
            <Form.Item
              label="Tá»ng tiá»n"
              style={{ marginLeft: 10 }}
              name="TONG_TIEN"
            >
              <InputNumber
                onKeyDown={handleEnter}
                style={{ width: 190 }}
                formatter={(value) =>
                  `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                }
                parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
              />
            </Form.Item>
            <Form.Item label="Ghi ChÃº" name="NOTE">
              <Input.TextArea style={{ width: 210 }} />
            </Form.Item>
          </Col>
        </Row>

        <Form.Item>
          <Button
                accessKey="s"
                type="primary"
                htmlType="submit"
                style={{ marginLeft: 20 }}
              >
                {serNo ? "Sá»­a" : "ThÃªm"}
              </Button>
              {serNo ? (
                <Button type="link" onClick={setField}>
                  Cancel
                </Button>
              ) : null}
        </Form.Item>
      </Form>
      <Spin size='large' spinning={isLoading}>
      <Table dataSource={state.data} columns={columns} pagination={false} />
      </Spin>
    </Fragment>
  );
};

export default CreateItemJobBookTau;
