import { FETCH_PHIEU_THU } from "../actions/actionTypes";

var initialState = [];

const phieuthu = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_PHIEU_THU:
      state = action.phieuthu;
      return [...state];
    default:
      return state;
  }
};

export default phieuthu;
